html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

body {
  background: -webkit-gradient(linear, right top, left top, from(#004c66), to(#005f7f));
  background: -webkit-linear-gradient(right, #004c66, #005f7f);
  background: -o-linear-gradient(right, #004c66, #005f7f);
  background: linear-gradient(to left, #004c66, #005f7f);
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#register {
  max-width: 360px;
  padding: 10px;
}

#register, #login {
  border: 1px solid #d6d7da;
  
  border-radius: 5px;
  font-family: arial;
  line-height: 16px;
  color: #333333;
  font-size: 14px;
  background: #ffffff;
  margin: 100px auto;
}

form {
 
  margin: 10px;
  padding: 10px;
 
}

input {
  width: 300px;
  border-radius: 3px;
  
}

form input {
  padding: 10px;
  border: solid 1px #BDC7D8;
  
}

.button {
  margin-top: 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #00BFFF;
  border: solid 1px #BDC7D8;
  font-weight: bold;
  height: 50px;
  color: #ffffff;
  min-width: 324px;
  border-radius: 3px;
  
}

.button:hover {
  background-color: #0099cc;
}

.button:active {
  background-color: #ffffff;
  color: #0099cc;
}

.div-button{
  position: relative;
}

.addAnotherPhone {
  content: '';
  display: block;
  height: 10px;
  width: 10px;
  border:0;
  cursor: pointer;
  border-radius: 10px;
  background: #40454f;
  position: absolute;
  top: 29px;
  right: 6px;
  outline: none;
  
  
}

.addAnotherPhone:hover{
  background: #000000;
  width: 24px;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
}

.addAnotherPhone:active{
  background: #0099cc;
}



.errorMsg {
  color: #cc0000;
  margin-bottom: 12px;
}

.sucessMsg {
  color: #6B8E23;
  margin-bottom: 10px;
}

.input[type="tel"] {
  position: relative;
}

.input[type="tel"]::before {
  content: '';
  display: block;
  position: absolute;
  right: 0;
  top: 0;
  height: 10px;
  background-color: #000000;
  color: #000;
}



